          {{i.replace(".txt",'')}}
        </a>
      </span>
    </div>
    <div class="badtable">
      <span v-for="i in Cazy_Daeje">
        {{i.replace(".txt",'')}}
      </span>
    </div>
    <div class="badtablebig">
